@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@600&display=swap");

.app {
  padding: 30px;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  font-family: "Poppins", sans-serif;
}

.game {
  display: flex;
  flex-wrap: wrap;
  width: 560px;
  height: 560px;
  margin: 0 auto;
}

.game img {
  width: calc(560px / 8);
  height: calc(560px / 8);
}
